.form-address-section {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    min-height: calc(100vh - 32.9rem);
}

.form-address-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: flex-start;
}

.form-address-content {
    border: 1px solid var(--primary_background);
    border-radius: 2.5rem;
    padding: 2rem 4rem;
    height: auto;
    min-height: 37rem;
}

.form-address {
    margin-bottom: 2.5rem;
}

.form-address h1 {
    color: var(--heading_primary);
}

.form-address p {
    color: var(--heading_secondary);
}

.form-address-manually-btn {
    display: flex;
    justify-content: flex-end;
}

.form-address-manually-btn button {
    border: none;
    color: var(--tertiary_background);
    background-color: transparent;
    cursor: pointer;
    transition: all 0.4s;
}

.form-address-manually-btn button:hover {
    color: var(--text_dark);
}

/* Inputs */
.form-address-input-content h2 {
    color: var(--heading_secondary);
}

.form-address-input {
    display: flex;
    flex-direction: column;
    margin-top: 1.5rem;
    color: var(--text_dark);
}

.form-address-input:first-of-type {
    margin-top: 2.5rem;
}

.form-address-input input {
    width: 23rem;
    padding: 1rem;
    border: 1px solid var(--primary_background);
    background-color: var(--white);
}

/* Buttons */
.form-address-btn-container {
    display: flex;
    justify-content: space-between;
    margin-top: 2.5rem;
}

.form-address-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    height: 4rem;
}

.btn-next {
    background-color: var(--tertiary_background);
    color: var(--text_white);
    font-weight: bold;
    padding-left: 5rem;
    padding-right: 2.5rem;
    border: 1px solid var(--tertiary_background);
    transition: all 0.4s;
}

.btn-next:hover {
    background-color: var(--white);
    border: 1px solid var(--tertiary_background);
    color: var(--primary_color);
}

.icon-next {
    transition: all 0.4s;
}

.btn-next:hover .icon-next {
    fill: var(--tertiary_background);
}

.btn-back {
    background-color: var(--white);
    border: 1px solid var(--tertiary_background);
    color: var(--tertiary_background);
    font-weight: bold;
    padding-left: 2.5rem;
    padding-right: 5rem;
    transition: all 0.4s;
}

.icon-back {
    transition: all 0.4s;
}

.btn-back:hover {
    background-color: var(--tertiary_background);
    color: var(--text_white);
}

.btn-back:hover .icon-back {
    fill: var(--white);
}

/* Script */
.script-address {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 50rem;
    margin-top: 2.5rem;
}

/* Assistant */
.assistant-address {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 30rem;
    margin-top: 2.5rem;
}

.assistant-address-custom {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 30rem;
    margin-top: 2.5rem;
}

.helper-address {
    margin: 0 auto;
}

/* Form Address Tooltip  */
.form-address-tooltip {
    opacity: 0;
    position: absolute;
    transition: all .3s;
    background-color: var(--tertiary_background);
    padding: .5rem 2rem;
    border-radius: .6rem;
    color: var(--white);
    font-size: 1.3rem;
    top: 28px;
    left: 236px;
}

.form-address-tooltip::after {
    content: " ";
    position: absolute;
    top: 50%;
    right: 100%;
    /* To the left of the tooltip */
    margin-top: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent var(--tertiary_background) transparent transparent;
}

.form-address-input:hover .form-address-tooltip {
    opacity: 1;
}

/* Tariff info */
.swal-modal_address {
    color: var(--primary_color);
}

.form-address-details-info-img img {
    width: 15rem;
    margin-bottom: 1rem;
}

.form-address-details-container {
    display: flex;
    flex-direction: column;
}

.form-address-details-info h3 {
    text-align: start;
    margin-left: 1rem;
    margin-bottom: 0;
    font-size: 2.2rem;
    color: var(--heading_primary);
    text-decoration: underline;
}

.form-address-single-details {
    display: flex;
    color: var(--text_dark);
    padding: 1rem;
    border-bottom: .5px solid rgba(49, 20, 91, .2);
}

.form-address-single-details p {
    font-weight: bold;
    margin: 0;
}

.form-address-single-details span {
    font-weight: normal;
    margin-left: 1rem;
}

/** MEDIAQUERIES*/
@media (min-width: 576px) {}

@media (min-width: 768px) {
    .form-address-section {
        width: 750px;
    }

    .assistant-address-custom {
        margin-left: 2rem;
        height: 50rem;
    }

    .helper-address {
        display: flex;
    }
}

@media (min-width: 992px) {
    .form-address-section {
        width: 970px;
    }

    .form-address-container {
        flex-direction: row;
    }

    .script-address {
        margin-top: 0;
        margin-left: 2rem;
    }

    .assistant-address {
        margin-top: 0;
        margin-left: 2rem;
    }

    .assistant-address-custom {
        margin-left: 2rem;
        height: 35rem;
    }

    .helper-address {
        width: 100%;
        flex-direction: column;
    }
}

@media (min-width: 1200px) {
    .form-address-section {
        width: 1170px;
    }
}